
.home {
  .home-base {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    padding-top: 100px;

    .ui-name-en {
      font-size: 26px;
      font-weight: bolder;
      span {
        color: #4fc08d;
      }
    }

    .ui-name-cn {
      font-size: 20px;
      margin: 30px 0;
      line-height: 1.5;
      text-align: center;
    }

    .ui-button {
      margin-top: 20px;
      width: 300px;
      display: flex;
      justify-content: space-between;
      margin: 0 auto;
    }
  }

  .home-markdown{
    width: 500px;
    margin: 50px auto;
    padding-top: 1px;

    .home-markdown-title{
      font-size: 23px;
      text-align: center;

      margin: 20px 0;
    }
  }
}
